<template>
	<div class="collect">
		<!--顶部菜单-->
		<nav-bar class="order-nav-bar">
			<div slot="center">
				<h4>我的收藏</h4>
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--菜单点击按钮-->
			<order-tab :text-arr="test" class="order-tab" @orderTabClick="orderTabClick"/>
						<!--滚动--->
		<scroll class="detail-scroller"
				ref="scroller"
				:probe-type=3
		>
			<content-nav :test="ordersArr"/>
		</scroll>
	</div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'
import OrderTab from 'components/contents/orderTab/OrderTab'
import ContentNav from './childComps/ContentNav'
import Scroll from 'components/common/scroll/Scroll'	

export default {
  name: 'Collect',
  components:{
	NavBar,
	OrderTab,
	ContentNav,
	Scroll
  },
  data(){
	return{
		test:['全部','已降价','上架中','已失效'],
		currentType:'tohdayOrder'
	}
  },
  methods:{
	orderTabClick(index){
		//这里获取内容
		switch(index){
			case 0:
				this.currentType="tohdayOrder"
				break;
			case 1:
				this.currentType="waitSendGoods"
				break;
			case 2:
				this.currentType="waitPayMoney"
				break;
			case 3:
				this.currentType="allOrders"
				break;
		}
		console.log(this.currentType)
	}
  },
   computed:{
	ordersArr(){
		return this.$store.state.collectList[this.currentType].list
	}
  }

}
</script>


<style>
.collect{
	position:relative;
	background:#fff;
	z-index:11;
	height:100vh;
}
 .detail-scroller{
    position: absolute;
	height:calc(100% - 95px);
    right: 0;
    left: 0;
	overflow:hidden;
  }

</style>
